
@keyframes fadeIn {
from {
		opacity: 0;
}
to {
		opacity: 1;
}
}
img[lazy=loaded] {
	animation-duration: 1s;
	animation-fill-mode: both;
	animation-name: fadeIn;
}
img[lazy=loading] {
	margin: auto;
	border: none;
}
img[lazy=error] {
	border-radius: 2px;
	animation-duration: 1s;
	animation-fill-mode: both;
	animation-name: fadeIn;
}
.bg-box {
	height: 0;
	background-size: cover;
	padding-bottom: 100%;
}
.bg-box[lazy=loading] {
	background-size: 10%;
	background-repeat: no-repeat;
	background-position: center center;
	background-color: #f1f1f1;
}
.bg-box[lazy=loaded] {
	background-size: cover;
	animation-duration: 1s;
	animation-fill-mode: both;
	animation-name: fadeIn;
	padding-bottom: 0;
}



