  color: #fff;
}
.index .mu-appbar {
  height: 45px;
}
.index .mu-appbar .mu-appbar-title {
  text-align: center;
}
.index .mu-appbar .mu-appbar-title span {
  font-size: 18px;
}
.index .mu-paper {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
}



